import { useDispatch } from "react-redux";
import { usernameGenerate } from "../fn/utils";
// import { app, nakama } from "../nakama";
import { accountActions } from "../redux/account";
import { pageActions } from "../redux/page";
import { presence_client } from "../server/presence";

const GuestLogin = ({ authRpc }) => {
  const dispatch = useDispatch();
  const signIn = async () => {
    const auth = await presence_client.authenticateCustom(
      Date.now().toString(),
      true,
      usernameGenerate()
    );
    const socket = presence_client.createSocket();

    socket.ondisconnect = (err) => {
      if (!err.code) return;
      window.pc.app.scenes.changeScene("_Login");
      // this.success(res);
    };
    const connected = await socket.connect(auth, true);

    if (connected) {
      // nakama.presence = socket;
      // nakama.presence_session = auth;
      authRpc(socket);
      // addReceivers(nakama.presence);
      const user_info = await presence_client.getAccount(connected);
      const metadata = JSON.parse(user_info.user.metadata);

      if (!metadata.prescription) {
        dispatch(pageActions.changePage(1));
      } else if (!metadata.last_map) {
        dispatch(pageActions.changePage(2));
        // app.fire("avatar");
      } else {
        dispatch(pageActions.changePage(-1));
        // window.pc.app.scenes.changeScene("_Master");
      }
      // const account = await nakama.refreshAccount();
      // dispatch(accountActions.refreshAccount(account));
    } else {
      console.error("error_connect_fail");
    }
  };

  const fixed_login = async () => {
    const auth = await presence_client.authenticateDevice(
      "48656C6C6F23376F726C64",
      true,
      "ImGMCenter"
    );
    const socket = presence_client.createSocket();
    const connected = await socket.connect(auth, true);
    if (connected) {
      // nakama.presence = socket;
      // nakama.presence_session = auth;
      authRpc(socket);
      // addReceivers(nakama.presence);
      const user_info = await presence_client.getAccount(connected);
      const metadata = JSON.parse(user_info.user.metadata);
      // nakama.refreshAccount = async () => {
      //   return await presence_client.getAccount(nakama.presence_session);
      // };
      socket.ondisconnect = (err) => {
        if (!err.code) return;
        // window.pc.app.scenes.changeScene("_Login");
        signIn();
      };
      if (!metadata.prescription) {
        dispatch(pageActions.changePage(1));
      } else if (!metadata.last_map) {
        dispatch(pageActions.changePage(2));
        // app.fire("avatar");
      } else if (metadata.gm_center_admin) {
        dispatch(pageActions.changePage(9));
      } else {
        dispatch(pageActions.changePage(-1));
        // window.pc.app.scenes.changeScene("_Master");
      }
      // const account = await nakama.refreshAccount();
      // dispatch(accountActions.refreshAccount(account));
    } else {
      console.error("error_connect_fail");
    }
  };
  return (
    <>
      <div
        className="d_f jc_center ai_center"
        id="guest-login-api"
        onClick={signIn}
      >
        
      </div>
      <div
        className="d_f jc_center ai_center"
        id="guest-login-api"
        onClick={fixed_login}
      >
        GM
      </div>
    </>
  );
};
export default GuestLogin;
