import { useState, useEffect } from "react";
import style from "./AdsPage.module.css";

const AdsPage = ({ data, width = 100, height = 320 }) => {
  useEffect(() => {
    let ins = document.createElement("ins");
    let scr = document.createElement("script");
    ins.className = "kakao_ad_area";
    ins.style = "display:none; width:100%;";
    scr.async = "true";
    scr.type = "text/javascript";
    scr.src = "//t1.daumcdn.net/kas/static/ba.min.js";
    ins.setAttribute("data-ad-width", height);
    ins.setAttribute("data-ad-height", width);
    ins.setAttribute("data-ad-unit", data);
    document.querySelector(".adfit").appendChild(ins);
    document.querySelector(".adfit").appendChild(scr);
  }, []);

  return <div className="adfit"></div>;
};

export default AdsPage;
