import React, { useState } from "react";
import style from "./LoginPage.module.css";
// import AppleLogin from "../../Utils/auth/appleAuth";
// import GoogleLogin from "../../Utils/auth/googleAuth";
import KakaoLogin from "../../Utils/auth/kakaoAuth";
import { useEffect } from "react";
import GuestLogin from "../../Utils/auth/guestAuth";
// import { onChannelMessage, onNotification } from "../../Utils/server/receivers";
import { nakama } from "../../nakama";
import { presence_client } from "../../Utils/server/presence";
import { useDispatch } from "react-redux";
import { pageActions } from "../../Utils/redux/page";
import TagManager from "react-gtm-module";
import { accountActions } from "../../Utils/redux/account";
import { usernameGenerate } from "../../Utils/fn/utils";
import { WebSocketAdapterPb } from "@heroiclabs/nakama-js-protobuf";
import AdsPage from "../../adsPage/AdsPage";

const LoginPage = () => {
  const [localIp, setLocalIp] = useState("");
  // const ipcheck = async () => {
  //   const { ip } = await (
  //     await fetch("https://jsonip.com", { mode: "cors" })
  //   ).json();
  //   setLocalIp(ip);
  // };
  const dispatch = useDispatch();
  const healthCheck = async () => {
    let isError = false;
    try {
      await presence_client.rpcHttpKey("", "healthCheck");
    } catch (err) {
      // console.log(err);
      if (!err.status) {
        isError = true;
      }
    }
    isError && dispatch(pageActions.changePage(10));
  };
  const authRpc = (socket) => {
    // const url = new URL(window.location);
    // const params = url.searchParams;
    // const user_ci = params.get("ci");
    // const from = params.get("from");
    // const pi = params.get("pi");
    // if (user_ci)
    //   socket.rpc("complete_sol_auth", JSON.stringify({ user_ci: user_ci }));
    // if (from) {
    //   socket.rpc("from_reward", JSON.stringify({ from: from }));
    // }
    // if (pi) {
    //   socket.rpc("set_facebook_id", JSON.stringify({ pi: pi }));
    // }
  };

  const customLogin = async () => {
    const url = new URL(window.location);
    const params = url.searchParams;

    const custom_id = params.get("custId");

    if (!custom_id || !custom_id.includes("-")) return;

    const auth = await presence_client.authenticateCustom(
      custom_id,
      false,
      usernameGenerate()
    );

    const socket = presence_client.createSocket(
      String(process.env.REACT_APP_PRESENCE_USESSL) === "true",
      false,
      new WebSocketAdapterPb()
    );

    // socket.ondisconnect = (err) => {
    //   if (!err.code) return;
    //   // window.pc.app.scenes.changeScene("_Login");
    // };

    const connected = await socket.connect(auth, true);

    if (connected) {
      nakama.presence = socket;
      nakama.presence_session = auth;
      authRpc(socket);
      // addReceivers(nakama.presence);
      const user_info = await presence_client.getAccount(connected);
      const metadata = JSON.parse(user_info.user.metadata);
      nakama.refreshAccount = async () => {
        return await presence_client.getAccount(nakama.presence_session);
      };

      if (!metadata.prescription) {
        dispatch(pageActions.changePage(1));
      } else if (!metadata.last_map) {
        dispatch(pageActions.changePage(2));
        // app.fire("avatar");
      } else {
        dispatch(pageActions.changePage(-1));
        // window.pc.app.scenes.changeScene("_Master");
      }
      const account = await nakama.refreshAccount();
      dispatch(accountActions.refreshAccount(account));
    } else {
      console.error("error_connect_fail");
    }
  };

  useEffect(() => {
    healthCheck();
    initKakao();
    customLogin();
    // app.graphicsDevice.maxPixelRatio = 1;
    TagManager.dataLayer({ dataLayer: { event: `login_page` } });
    return () => {
      const trash = document.getElementById("g_a11y_announcement");
      const trashFrame = [...document.getElementsByTagName("iframe")];
      trashFrame.forEach((el) => el.remove());
      trash && trash.remove();
    };
  }, []);

  const initKakao = () => {
    if (!window.Kakao.isInitialized()) {
      window.Kakao.init("43abbb1610b49f14fba3de05e5fbfadf");
    }
  };
  return (
    <div className={style.container}>
      <AdsPage data={"DAN-t1g3lWZhc4BrIm0E"} />
      <div className={style.bubble_container}>
        {/* <SpeechBubble
          type={"bold"}
          text={"    ! \n   "}
        /> */}
      </div>
      <div className={style.social_container}>
        <div className={style.text}> </div>
        <div className={style.btn_container}>
          {/* <GoogleLogin
            authRpc={authRpc}
            addReceivers={addReceivers}
            localIp={localIp}
          />
          <AppleLogin
            authRpc={authRpc}
            addReceivers={addReceivers}
            localIp={localIp}
          /> */}
          <KakaoLogin
            authRpc={authRpc}
            // addReceivers={addReceivers}
            localIp={localIp}
          />
          {!window.origin.includes("https") && <GuestLogin authRpc={authRpc} />}
        </div>
      </div>
      {/* {pwa && (
        <div className={style.con_pwa}>
          <div className={style.pwa_mainText}>  </div>
          <div className={style.pwa_subText}>
                ?
          </div>
          <div className={style.pwa_con_logo}>
            <img src={"/logo180.png"} alt={"shinamon"} />
            <div></div>
          </div>
          <div className={style.pwa_con_btn}>
            <div className={style.pwa_white_btn} onClick={onClickCancel}>
              
            </div>
            <div className={style.pwa_purple_btn} onClick={onClickEvent}>
              
            </div>
          </div>
        </div>
      )} */}
    </div>
  );
};

export default LoginPage;
