import React from "react";
import { useDispatch } from "react-redux";
import { usernameGenerate } from "../fn/utils";
import { nakama } from "../../nakama";
import { pageActions } from "../redux/page";
import { accountActions } from "../redux/account";
import { presence_client } from "../server/presence";
import style from "./Auth.module.css";
import TagManager from "react-gtm-module";
import { WebSocketAdapterPb } from "@heroiclabs/nakama-js-protobuf";

const KakaoLogin = ({ authRpc, addReceivers, localIp }) => {
  const dispatch = useDispatch();
  const kakaoLoginRequest = () => {
    window.Kakao.Auth.login({
      success() {
        window.Kakao.API.request({
          url: "/v2/user/me",
          async success(res) {
            const token = window.Kakao.Auth.getAccessToken();
            const id = res.id;
            const auth = await presence_client.authenticateDevice(
              id.toString().concat(".kakao"),
              true,
              usernameGenerate(res.properties.nickname),
              { ip: localIp }
            );
            const socket = presence_client.createSocket(
              String(process.env.REACT_APP_PRESENCE_USESSL) === "true",
              false
            );
            socket.ondisconnect = (err) => {
              if (!err.code) return;
              this.success(res);
            };

            const connected = await socket.connect(auth, true);

            if (connected) {
              // TagManager.dataLayer({ dataLayer: { event: "kakao_login" } });
              console.log(auth);
              nakama.presence = socket;
              nakama.presence_session = auth;
              authRpc(socket);
              // addReceivers(nakama.presence);
              const user_info = await presence_client.getAccount(connected);
              const metadata = JSON.parse(user_info.user.metadata);
              nakama.refreshAccount = async () => {
                return await presence_client.getAccount(
                  nakama.presence_session
                );
              };
              if (!metadata.prescription) {
                dispatch(pageActions.changePage(1));
              } else if (!metadata.last_map) {
                dispatch(pageActions.changePage(2));
                // app.fire("avatar");
              } else {
                dispatch(pageActions.changePage(-1));
              }
              const account = await nakama.refreshAccount();
              dispatch(accountActions.refreshAccount(account));
            } else {
              console.error("error_connect_fail");
            }
          },
          fail(error) {
            console.log(error);
          },
        });
      },
      fail(error) {
        console.log(error);
      },
    });
  };
  return (
    <div
      id="kakao-login-api"
      onClick={kakaoLoginRequest}
      className={style.center}
    >
      <img src="images/icon/icon_kakao.webp" alt="kakao" width={40} />
    </div>
  );
};
export default KakaoLogin;
