import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import Spinner from "../../Spinner/Spinner";
// import GameSpinner from "../../Spinner/GameSpinner";
import { pageActions } from "../../Utils/redux/page";
import style from "./LoadingPage.module.css";

const LoadingPage = ({ once, setOnce, pageNum = 0 }) => {
  const dispatch = useDispatch();

  useEffect(() => {
    setOnce(false);
    dispatch(pageActions.changePage(pageNum));
    return () => {
      // clearTimeout(timer);
      dispatch(pageActions.changePage(pageNum));
      setOnce(false);
    };
  });
  return (
    <>
      {once ? (
        <div className={style.container}>
          <div className={style.logo_container}>
            <div className={style.img_container}>
              <div className={style.left}>
                .
                {/* <img
                  className="img_50"
                  src={"images/icon/img_logo_splash_off.webp"}
                  alt="off"
                /> */}
              </div>
            </div>
            {/* <img
              className={style.logo}
              src={"images/photo/shinamon.webp"}
              alt="logo"
            /> */}
          </div>
        </div>
      ) : (
        <div className={style.container}>
          <Spinner />
        </div>
      )}
    </>
  );
};

export default LoadingPage;
